.rent-items {
  width: 100%;
  margin-bottom: 50px;
  background: #f5f5f5f5;
  overflow: hidden;
  position: relative;
  padding-bottom: 20px;
  text-align: center;
}

.rent-items__img {
  width: 100%;
  height: 100%;
}

.rent-items__title {
  display: block;
  margin-top: 15px;
}

.rent-items-day {
  padding-left: 10px;
}

.icon-search{
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #f5f5f5f5;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: transform 500ms ease;
  }
  
  .icon-search:hover{
  transform: scale(1.1);
  } 
  
   .icon-background{
    opacity: 0;
    width: 100%;
    height: 65%;
    position: absolute;
    top:0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.2);
    z-index: 3;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: all 500ms ease;
  }
  
  .icon-background:hover{
    opacity: 1
  }